import { Log } from '../util/log';

export class BoardPoint {

  static readonly STATE_NONE = 0;
  static readonly STATE_LINE = 1;
  static readonly STATE_CROSS = 2;

  static readonly LINK_NONE = 0;
  static readonly LINK_THIS = 1;
  static readonly LINK_TOP = 2;
  static readonly LINK_BOTTOM = 3;
  static readonly LINK_RIGHT = 4;
  static readonly LINK_LEFT = 5;

  private readonly TAG = 'BoardPoint';

  private top = BoardPoint.STATE_NONE;
  private right = BoardPoint.STATE_NONE;
  private bottom = BoardPoint.STATE_NONE;
  private left = BoardPoint.STATE_NONE;

  private lines = 0;

  links = {
    top: {
      right: BoardPoint.LINK_NONE,
      left: BoardPoint.LINK_NONE
    },
    bottom: {
      right: BoardPoint.LINK_NONE,
      left: BoardPoint.LINK_NONE
    },
    right: {
      top: BoardPoint.LINK_NONE,
      bottom: BoardPoint.LINK_NONE
    },
    left: {
      top: BoardPoint.LINK_NONE,
      bottom: BoardPoint.LINK_NONE
    }
  };

  /**
   * @description
   * Constructor.
   */
  constructor() {
  }

  onTopClick() {
    Log.v(this.TAG, 'onTopClick: start');

    this.top++;
    if (BoardPoint.STATE_CROSS < this.top) {
      this.top = BoardPoint.STATE_NONE;
    }
    Log.v(this.TAG, 'onTopClick: top: ' + this.top);

    this.setLines();

    Log.v(this.TAG, 'onTopClick: end');
    return this.top;
  }

  onRightClick(): number {
    Log.v(this.TAG, 'onRightClick: start');

    this.right++;
    if (BoardPoint.STATE_CROSS < this.right) {
      this.right = BoardPoint.STATE_NONE;
    }
    Log.v(this.TAG, 'onRightClick: right: ' + this.right);

    this.setLines();

    Log.v(this.TAG, 'onRightClick: end');
    return this.right;
  }

  onBottomClick(): number {
    Log.v(this.TAG, 'onBottomClick: start');

    this.bottom++;
    if (BoardPoint.STATE_CROSS < this.bottom) {
      this.bottom = BoardPoint.STATE_NONE;
    }
    Log.v(this.TAG, 'onBottomClick: bottom: ' + this.bottom);

    this.setLines();

    Log.v(this.TAG, 'onBottomClick: end');
    return this.bottom;
  }

  onLeftClick(): number {
    this.left++;
    if (BoardPoint.STATE_CROSS < this.left) {
      this.left = BoardPoint.STATE_NONE;
    }
    this.setLines();

    return this.left;
  }

  getTop() {
    return this.top;
  }

  getRight() {
    return this.right;
  }

  getBottom() {
    return this.bottom;
  }

  getLeft() {
    return this.left;
  }

  /**
   * .
   *
   * @returns
   */
  getLines(): number {
    return this.lines;
  }

  private setLines(): void {
    Log.v(this.TAG, 'setLines: start');

    this.lines = 0;
    if (1 === this.top) {
      this.lines++;
    }
    if (1 === this.right) {
      this.lines++;
    }
    if (1 === this.bottom) {
      this.lines++;
    }
    if (1 === this.left) {
      this.lines++;
    }
    Log.v(this.TAG, 'setLines: lines: ' + this.lines);

    Log.v(this.TAG, 'setLines: end');
  }

}
