import * as moment from 'moment';

export class Log {

  private static readonly VERBOSE = 0x00000002;
  private static readonly DEBUG = 0x00000003;
  private static readonly INFO = 0x00000004;
  private static readonly WARN = 0x00000005;
  private static readonly ERROR = 0x00000006;
  private static readonly ASSERT = 0x00000007;

  private static readonly PRIORITY = Log.DEBUG;

  /**
   * @description
   * Constructor.
   */
  constructor() {
  }

  /**
   * @description
   * Send a VERBOSE log message.
   *
   * @param tag Used to identify the source of a log message.
   * @param message The message you would like logged.
   */
  static v(tag: string, message: string) {
    this.log(Log.VERBOSE, tag, message);
  }

  /**
   * @description
   * Send a DEBUG log message.
   *
   * @param tag Used to identify the source of a log message.
   * @param message The message you would like logged.
   */
  static d(tag: string, message: string) {
    this.log(Log.DEBUG, tag, message);
  }

  /**
   * @description
   * Send a INFO log message.
   *
   * @param tag Used to identify the source of a log message.
   * @param message The message you would like logged.
   */
  static i(tag: string, message: string) {
    this.log(Log.INFO, tag, message);
  }

  /**
   * @description
   * Send a WARN log message.
   *
   * @param tag Used to identify the source of a log message.
   * @param message The message you would like logged.
   */
  static w(tag: string, message: string) {
    this.log(Log.WARN, tag, message);
  }

  /**
   * @description
   * Send a ERROR log message.
   *
   * @param tag Used to identify the source of a log message.
   * @param message The message you would like logged.
   */
  static e(tag: string, message: string) {
    this.log(Log.ERROR, tag, message);
  }

  /**
   * @description
   * Send a ASSERT log message.
   *
   * @param tag Used to identify the source of a log message.
   * @param message The message you would like logged.
   */
  static a(tag: string, message: string) {
    this.log(Log.ASSERT, tag, message);
  }

  private static log(priority: number, tag: string, message: string) {
    if (Log.PRIORITY > priority) {
      return;
    }
    message = ('/' + tag + ': ' + message);
    if (priority === Log.VERBOSE) {
      message = 'V' + message;
    } else if (priority === Log.DEBUG) {
      message = 'D' + message;
    } else if (priority === Log.INFO) {
      message = 'I' + message;
    } else if (priority === Log.WARN) {
      message = 'W' + message;
    } else if (priority === Log.ERROR) {
      message = 'E' + message;
    } else if (priority === Log.ASSERT) {
      message = 'A' + message;
    } else {
      return;
    }
    console.log(moment().format('MM-DD HH:mm:ss.SSS') + ' ' + message);
  }

}
