  /**
   * .
   */
  verify() {
    Log.d(this.TAG, 'verify: start');

    for (let row = 0; row < this.BOARD_ROWS; row++) {
      for (let column = 0; column < this.BOARD_COLUMNS; column++) {
        // .
        const point = this.points[row][column];

        // .
        const lines = point.getLines();
        Log.d(this.TAG, 'verify: [' + row + '][' + column + ']: lines: ' + lines);
        if (4 === lines) {
          Log.i(this.TAG, 'verify: Impossible line count.');
          Log.d(this.TAG, 'verify: end: false');
          return false;
        }

        // .
        const no = this.puzzle[row][column];
        Log.d(this.TAG, 'verify: [' + row + '][' + column + ']: no: ' + no);
        if ('' !== no) {
          if (Number(no) !== lines) {
            Log.i(this.TAG, 'verify: Line count mismatch.');
            Log.d(this.TAG, 'verify: end: false');
            return false;
          }
        }

        // .
        if (!this.verifyLink(row, column, point)) {
          Log.d(this.TAG, 'verify: end: false');
          return false;
        }
      }
    }

    Log.d(this.TAG, 'verify: end: true');
    return true;
  }

  /**
   * .
   *
   * @param row
   * @param column
   * @param point
   */
  verifyLink(row: number, column: number, point: BoardPoint): boolean {
    Log.v(this.TAG, 'verifyLink: start');

    if (!this.verifyTopLink(row, column, point)) {
      Log.v(this.TAG, 'verifyLink: end: false');
      return false;
    }
    if (!this.verifyBottomLink(row, column, point)) {
      Log.v(this.TAG, 'verifyLink: end: false');
      return false;
    }
    if (!this.verifyRightLink(row, column, point)) {
      Log.v(this.TAG, 'verifyLink: end: false');
      return false;
    }
    if (!this.verifyLeftLink(row, column, point)) {
      Log.v(this.TAG, 'verifyLink: end: false');
      return false;
    }

    Log.v(this.TAG, 'verifyLink: end: true');
    return true;
  }

  /**
   * .
   *
   * @param row
   * @param column
   * @param point
   */
  verifyTopLink(row: number, column: number, point: BoardPoint): boolean {
    Log.v(this.TAG, 'verifyTopLink: start');

    point.links.top.right = BoardPoint.LINK_NONE;
    point.links.top.left = BoardPoint.LINK_NONE;

    if (BoardPoint.STATE_LINE !== point.getTop()) {
      Log.v(this.TAG, 'verifyTopLink: end: true');
      return true;
    }

    let linkRight = 0;
    let linkLeft = 0;

    if (BoardPoint.STATE_LINE === point.getRight()) {
      linkRight++;
      point.links.top.right = BoardPoint.LINK_THIS;
    }

    if (BoardPoint.STATE_LINE === point.getLeft()) {
      linkLeft++;
      point.links.top.left = BoardPoint.LINK_THIS;
    }

    if ((this.BOARD_COLUMNS - 1) > column) {
      if (BoardPoint.STATE_LINE === this.points[row][(column + 1)].getTop()) {
        linkRight++;
        point.links.top.right = BoardPoint.LINK_RIGHT;
      }
    }

    if (0 < column) {
      if (BoardPoint.STATE_LINE === this.points[row][(column - 1)].getTop()) {
        linkLeft++;
        point.links.top.left = BoardPoint.LINK_LEFT;
      }
    }

    if (0 < row) {
      const pointTop = this.points[(row - 1)][column];
      if (BoardPoint.STATE_LINE === pointTop.getRight()) {
        linkRight++;
        point.links.top.right = BoardPoint.LINK_TOP;
      }
      if (BoardPoint.STATE_LINE === pointTop.getLeft()) {
        linkLeft++;
        point.links.top.left = BoardPoint.LINK_TOP;
      }
    }

    Log.v(this.TAG, 'verifyTopLink: linkRight: ' + linkRight);
    Log.v(this.TAG, 'verifyTopLink: linkLeft: ' + linkLeft);

    if ((1 !== linkRight) || (1 !== linkLeft)) {
      Log.i(this.TAG, 'verifyTopLink: Illegal links.');
      Log.v(this.TAG, 'verifyTopLink: end: false');
      return false;
    }

    Log.v(this.TAG, 'verifyTopLink: end: true');
    return true;
  }

  /**
   * .
   *
   * @param row
   * @param column
   * @param point
   */
  verifyBottomLink(row: number, column: number, point: BoardPoint): boolean {
    Log.v(this.TAG, 'verifyBottomLink: start');

    point.links.bottom.right = BoardPoint.LINK_NONE;
    point.links.bottom.left = BoardPoint.LINK_NONE;

    if (BoardPoint.STATE_LINE !== point.getBottom()) {
      Log.v(this.TAG, 'verifyBottomLink: end: true');
      return true;
    }

    let linkRight = 0;
    let linkLeft = 0;

    if (BoardPoint.STATE_LINE === point.getRight()) {
      linkRight++;
      point.links.bottom.right = BoardPoint.LINK_THIS;
    }

    if (BoardPoint.STATE_LINE === point.getLeft()) {
      linkLeft++;
      point.links.bottom.left = BoardPoint.LINK_THIS;
    }

    if ((this.BOARD_COLUMNS - 1) > column) {
      if (BoardPoint.STATE_LINE === this.points[row][(column + 1)].getBottom()) {
        linkRight++;
        point.links.bottom.right = BoardPoint.LINK_RIGHT;
      }
    }

    if (0 < column) {
      if (BoardPoint.STATE_LINE === this.points[row][(column - 1)].getBottom()) {
        linkLeft++;
        point.links.bottom.left = BoardPoint.LINK_LEFT;
      }
    }

    if ((this.BOARD_ROWS - 1) > row) {
      const pointBottom = this.points[(row + 1)][column];
      if (BoardPoint.STATE_LINE === pointBottom.getRight()) {
        linkRight++;
        point.links.bottom.right = BoardPoint.LINK_BOTTOM;
      }
      if (BoardPoint.STATE_LINE === pointBottom.getLeft()) {
        linkLeft++;
        point.links.bottom.left = BoardPoint.LINK_BOTTOM;
      }
    }

    Log.v(this.TAG, 'verifyBottomLink: linkRight: ' + linkRight);
    Log.v(this.TAG, 'verifyBottomLink: linkLeft: ' + linkLeft);

    if ((1 !== linkRight) || (1 !== linkLeft)) {
      Log.i(this.TAG, 'verifyBottomLink: Illegal links.');
      Log.v(this.TAG, 'verifyBottomLink: end: false');
      return false;
    }

    Log.v(this.TAG, 'verifyBottomLink: end: true');
    return true;
  }

